:root {
  --bg: #fffffe;
  --second: #e3f6f5;
  --judul: #272343;
  --p: #2d334a;
  --p2: #bae8e8;
  --button: #ffd803;
  --buttonText: #272343;
  --footer: #e16162;
}

*,
body {
  margin: 0;
  padding: 0;
}

.bg img {
  position: absolute;
  height: 100vh;
  top: 0;
  left: 0;
  background: url(../img/bg.jpg);
  background-size: cover;
  background-position: center;
  z-index: -1;
}

.bg img::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  background-color: black;
}

.overlay {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0.5;
  background-color: black;
}

.navbar {
  font-family: "Comme", sans-serif;
  width: 100%;
  height: 3rem;
  z-index: 99;
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
}

.logo {
  display: flex;
  margin: 20px;
  font-size: 2rem;
  font-weight: 700;
}

.logo a {
  font-style: italic;
  text-decoration: none;
  color: var(--bg);
}

.logo span {
  color: var(--button);
}

.menu {
  display: flex;
  margin: 20px;
  font-size: 1rem;
}

.menu a {
  text-decoration: none;
  color: var(--bg);
  margin: 10px;
  padding: 10px;
  text-align: center;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  transition: ease 0.3s;
  -webkit-transition: ease 0.3s;
  -moz-transition: ease 0.3s;
  -ms-transition: ease 0.3s;
  -o-transition: ease 0.3s;
}

.menu a:hover {
  background-color: var(--button);
  color: var(--buttonText);
}

.hero {
  display: flex;
  height: 70vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 40px;
  font-family: "Open Sans", sans-serif;
  animation: slideInLeft 1s ease-out;
  -webkit-animation: slideInLeft 1s ease-out;
}

.hero h1 {
  font-style: italic;
  color: var(--second);
  font-size: 48px;
  margin: 0;
  animation: slideInLeft 1.3s ease-out;
  -webkit-animation: slideInLeft 1.3s ease-out;
}

.hero h1 span {
  color: var(--button);
}

.hero p {
  color: var(--bg);
  font-size: 24px;
  margin-top: 10px;
  animation: slideInLeft 1.5s ease-out;
  -webkit-animation: slideInLeft 1.5s ease-out;
}

.hero .btn {
  display: inline-block;
  padding: 10px;
  width: 200px;
  border: solid 1px var(--button);
  color: var(--bg);
  text-decoration: none;
  font-size: 24px;
  margin-top: 30px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  transition: ease 0.3s;
  -webkit-transition: ease 0.3s;
  -moz-transition: ease 0.3s;
  -ms-transition: ease 0.3s;
  -o-transition: ease 0.3s;
  animation: slideInLeft 1.7s ease-out;
  -webkit-animation: slideInLeft 1.7s ease-out;
}

.hero .btn:hover {
  background-color: #b69b06;
  color: var(--buttonText);
}